import { useDarkModeManager } from '../contexts/Cookie'

const LIGHT_MODE = true
const DARK_MODE = false

// define theme colors
const WHITE = '#FFFFFF'
const BLACK = '#000000'
const BACKGROUNDS = {
  [LIGHT_MODE]: WHITE,
  [DARK_MODE]: BLACK
}
const TEXTS = {
  [LIGHT_MODE]: BLACK,
  [DARK_MODE]: WHITE
}
const LINKS = {
  [LIGHT_MODE]: '#0000EE',
  [DARK_MODE]: '#ffffff'
}

export default function useTheme() {
  const [isDarkMode] = useDarkModeManager()

  return {
    isDarkMode,
    colors: {
      background: BACKGROUNDS[isDarkMode],
      text: TEXTS[isDarkMode],
      link: LINKS[isDarkMode]
    }
  }
}
